<template>
  <div class="container1">
    <div class="doctorForm">
      <h3>添加医生账户</h3>
      <el-form ref="form" :model="doctorForm" label-width="80px">
        <el-form-item label="doctorid">
          <el-input v-model="doctorForm.doctorid"></el-input>
        </el-form-item>
        <el-form-item label="nickname">
          <el-input v-model="doctorForm.nickname"></el-input>
        </el-form-item>
     
      <el-form-item label="password">
        <el-input v-model="doctorForm.password"></el-input>
      </el-form-item>
      
      <el-form-item label="phone">
        <el-input v-model="doctorForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="department">
        <el-select v-model="doctorForm.department" placeholder="请选择">
          <el-option
            v-for="item in keshis"
            :key="item._id"
            :label="item.name"
            :value="item.name"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="gender">
        <el-select v-model="doctorForm.gender" placeholder="请选择">
          <el-option
            v-for="item in sex"
            :key="item.id"
            :label="item.name"
            :value="item.name"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="grade">
        <el-input v-model="doctorForm.grade"></el-input>
      </el-form-item>
       <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
      </el-form-item>
       </el-form>
    </div>
  </div>
</template>

<script>
import { getkeshi, postAllDoctor, changeState,AddDoctor } from "../../network/data";
export default {
  created() {
    getkeshi().then(res => {
      this.keshis = res;
      let arr = [];
      for (let item of res) {
        arr.push({ value: item.name, label: item.name });
      }
      this.options = arr;
    });
    postAllDoctor().then(res=>{
      this.doctorForm.doctorid=res.length+1
      console.log(this.doctorForm.doctorid);
      
    })
  },
  data() {
    return {
      sex:[
        {
          id:1,name:'男'
        },{
          id:2,name:'女'
        }
      ],
      doctorForm: {doctorid:''},
      options: [],
      CurrentKeshi: "",
      value1: new Date(),
      keshis: [],
      doctors: [],
      checkdoctors: [],
      value:""
    };
  },
  methods: {
    onSubmit(){
      AddDoctor(this.doctorForm).then(res=>{
        this.doctorForm={}
        this.$message({
          message: '添加成功',
          type: 'success'
        });
      })
    },
    send() {
      this.doctors = [];
      this.checkdoctors = [];
      let year = this.value1.getUTCFullYear();
      let month = this.value1.getUTCMonth() + 1;
      let day = this.value1.getDate();
      console.log(year, month, day, this.CurrentKeshi);
      postAllDoctor().then(res => {
        for (let item of res) {
          if (this.CurrentKeshi == item.department) {
            this.doctors.push(item);
          }
        }
        //console.log(this.doctors);
      });
    },
    add() {
      let number = [
        { num: 1, state: "可预约", date: "8:05-9:00" },
        { num: 2, state: "可预约", date: "9:00-10:00" },
        { num: 3, state: "可预约", date: "10:00-11:00" }
      ];
      //console.log(this.checkdoctors);
      let time = [];
      let year = this.value1.getUTCFullYear();
      let month = this.value1.getUTCMonth() + 1;
      let day = this.value1.getDate();
      let hasdate = false;
      if (month < 10) month = "0" + month;
      if (day < 10) day = "0" + day;
      let thedate = year + "-" + month + "-" + day;
      let id = "";
      let hasdoctor = false;
      for (let i = 0; i < this.keshis.length; i++) {
        if (this.CurrentKeshi == this.keshis[i].name) {
          id = this.keshis[i]._id;
          time = this.keshis[i].time;
          for (let j = 0; j < time.length; j++) {
            if (time[j].date == thedate) {
              hasdate = true;
              for (let k = 0; k < this.checkdoctors.length; k++) {
                hasdoctor = false;
                for (let a = 0; a < time[j].doctor.length; a++) {
                  if (
                    this.checkdoctors[k].doctorid === time[j].doctor[a].doctorid
                  ) {
                    hasdoctor = true;
                    break;
                  }
                }
                if (!hasdoctor) {
                  time[j].doctor.push({
                    doctorid: this.checkdoctors[k].doctorid,
                    name: this.checkdoctors[k].nickname,
                    number: number,
                    grade: this.checkdoctors[k].grade
                  });
                }
              }
              let obj = {
                id,
                time
              };
              console.log(obj)
              changeState(obj).then(res => {
                console.log(res);
              });
              break;
            }
          }
        }
      }
      if (!hasdate) {
        //如果选中日期不存在，上面无法添加成功，所以需要time.push(obj)，这个obj包含{doctor[],date,name}。
        let doc = [];
        for (let k = 0; k < this.checkdoctors.length; k++) {
          doc.push({
            doctorid: this.checkdoctors[k].doctorid,
            name: this.checkdoctors[k].nickname,
            number: number,
            grade: this.checkdoctors[k].grade
          });
        }

        let o = {
          name: month + "-" + day + "日",
          date: thedate,
          doctor: doc
        };
        time.push(o);
        let t = {
          id,
          time
        };
        changeState(t).then(res => {
          console.log(res);
        });
      }
    }
  }
};
</script>
<style>
.container1 {
  margin-left: 20px;
}
.block {
  margin-top: 8px;
}
.doctorForm {
  margin-top: 50px;
}
</style>